.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}
@keyframes fa-spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(359deg);
  }
}
.text-block {
  margin-bottom: 21px;
  margin-right: 15px;
}
.text-block:last-child {
  margin-bottom: 0;
}
.text-block__text {
  color: #343434;
  font-family: Tahoma, Arial, sans-serif;
  line-height: 24px;
  font-size: 13px;
}
.text-block__link {
  color: #bc0000;
  text-decoration: underline;
  font-family: Tahoma, Arial, sans-serif;
  font-size: 13px;
  margin-bottom: 5px;
  display: inline-block;
}
.text-block__link:hover {
  text-decoration: none;
}
.text-block__sub-title {
  font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
  font-size: 20px;
  line-height: 30px;
  color: #bc0000;
}
.text-block__sub-title--black {
  color: #343434;
  font-family: Tahoma, Arial, sans-serif;
  font-size: 13px;
  line-height: 24px;
  padding-right: 15px;
}
.card__item {
  width: 100%;
